import React, { PureComponent } from "react";
import { isTagEmbed } from "../../../../actions/api";

const NetworkStar = ({ network }) => {
    const networkStyles = {
        3: { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#1b74e4' },
        19: { className: 'tb_rating_ico__ tb__icon tb-yelp-star', color: '#e00707' },
        23: { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#ff385c' },
        28: isTagEmbed ? { className: 'tb_rating_ico__ tb__icon tb-star', color: '#F8B90C' } : { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#F8B90C' },
        29: isTagEmbed ? { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#000' } : { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#613983' },
        33: { className: 'tb_rating_ico__ tb__icon tb-trustpilot', color: '#000' },
        35: { className: 'tb_rating_ico__ tb__icon tb-star-fill', color: '#34E0A1' },
    };

    const { className, color } = networkStyles[network.id] || {
        className: 'tb_rating_ico__ tb__icon tb-default-star',
        color: '#F8B90C',
    };

    return <div className={className} style={{ color }} tb-network={network.id}><div></div></div>;
};

const ThemeStar = ({ ThemeID }) => {
    const themeStyles = {
        59: { className: 'tb_rating_ico__ tb__icon tb-star-fill', color: '#34E0A1' },
    };

    const { className, color } = themeStyles[ThemeID] || {
        className: 'tb_rating_ico__ tb__icon tb-star-alt',
        color: '#F8B90C',
    };

    return <div className={className} style={{ color }} tb-network={ThemeID}></div>;
};
export default class Rating extends PureComponent {
    render() {
        const { rating, network, ThemeID } = this.props;
        return (
            <div className="tb__rating__" aria-hidden>
                {
                    rating ? [...Array(rating)].map((el, index) => ThemeID ? <ThemeStar key={index} ThemeID={ThemeID} /> : <NetworkStar key={index} network={network} /> ) : null
                }
            </div>
        );
    }
}
